import React, { PropTypes } from 'react';
import styled from 'styled-components';
import mangopayImg from '../../assets/mangopay.png';
import { Center, Button3, SmallText, Link, Note } from '../styledComponents';

const Img = styled.img`
  object-fit: contain;
  width: 100%;
`;

const OrderFooter = ({ isValid, submit }) => (<div>
  <Center>
    <Button3
      isNotValid={!isValid}
      onClick={submit}
    >Valider mon RDV</Button3>
  </Center>
  <SmallText>
    {'En validant, vous acceptez les '}
    <Link href="#">
      conditions gnrales
    </Link>
    {' de vente de tilli'}
  </SmallText>
  <Note>
    {`(*) Le mimimun de commande est de 20. Si votre commande est infrieure  ce montant,
      des frais de livraison seront appliqus pour atteindre 25. Au dessus de 20, 5 de
      participation aux frais de livraison sont appliqus.`}
  </Note>
  <Img src={mangopayImg} alt="MangoPay notre partenaire de paiement" />
</div>);

OrderFooter.propTypes = {
  isValid: PropTypes.bool.isRequired,
  submit: PropTypes.func.isRequired,
};

export default OrderFooter;



